'use strict';

var React = require('react'),
    model = require('model/messages');

var Message = React.createClass({
  displayName: 'Message',

  onClick: function() {
    model.repeat(this.props.text);
  },

  render: function() {
    var classes = 'message';
    if (this.props.text == '…')
      classes += ' message_waiting';

    var tail = null;
    if (this.props.type == 0) {
      classes += ' message_ours';
      tail = <img className="message__tail" src="i/shape2.svg" />;
    } else if (this.props.type == 1) {
      classes += ' message_theirs';
      tail = <img className="message__tail" src="i/shape1.svg" />;
    }

    return (
        <div className={classes}
             key={'mes' + this.props.date + this.props.type}
             onClick={this.onClick}>
          {this.props.text}
          {tail}
        </div>
    );
  }
});

module.exports = Message;



