'use strict';

var React = require('react'),
    model = require('model/messages');

var Toolbar = React.createClass({
  displayName: 'Toolbar',

  getInitialState: function() {
    return {
      q: ''
    };
  },

  componentWillMount: function() {
    model.on('repeat', this.restoreMessage);
  },

  componentWillUnmount: function() {
    model.off('repeat', this.restoreMessage);
  },

  restoreMessage: function(message) {
    this.setState({q: message});
  },

  sendMessage: function() {
    if (!this.state.q)
      return;

    model.ask(this.state.q);
    this.setState({q: ''});
  },

  onKeyDown: function(e) {
    switch (e.which) {
      case 13: // enter
        this.sendMessage();
        break;
      case 32: // space
        break;
      case 8: //backspace
        break;
    }
  },

  onChange: function(e) {
    this.setState({q: e.target.value});
  },

  componentDidMount: function() {
    //var node = React.findDOMNode(this.refs.input);
  },

  render: function() {
    var q = this.state.q;

    return (
        <div className="toolbar">
          <div className="button-photo" />
          <input type="input" ref="input" className="search"
                 placeholder="iMessage"
                 onChange={this.onChange}
                 onKeyDown={this.onKeyDown}
                 value={q} />
          <div className="button-send"
               disabled={q ? false : true}
               onTouchEnd={this.sendMessage}>
            Send
          </div>
        </div>
    );
  }
});

module.exports = Toolbar;



