'use strict';

var TRACE = true;

var PROXY = 'https://cors-anywhere.herokuapp.com/';

function parseJSON(request) {
  return new Promise(function(resolve, reject) {
    try {
      var data = JSON.parse(request.responseText);
      resolve(data);
    } catch (err) {
      reject(err);
    }
  });
}

function parseXML(xml, extractionConfig) {
  if (!(xml instanceof Document)) {
    return null;
  }

  var result = {};
  var evaluator = new XPathEvaluator();
  extractionConfig.forEach(function(item) {
    var node = evaluator.evaluate(item.xpath, xml.documentElement, null,
                                  item.xpathResultType, null);
    var value;
    switch (item.xpathResultType) {
      case XPathResult.ANY_TYPE:
        value = node.iterateNext();
        break;
      case XPathResult.STRING_TYPE:
        value = node.stringValue;
        break;
      case XPathResult.NUMBER_TYPE:
        value = node.numberValue;
        break;
    }

    result[item.name] = value;
  });
  return result;
}

function requestURL(url, headers, responseType) {
  return new Promise(function(resolve, reject) {
    var req = new XMLHttpRequest();
    req.addEventListener('load', function() {
      if (req.status == 200) {
        resolve(req);
      } else {
        reject(Error('REST: Network error code: ' + req.status + ' ' +
                     req.statusText + ' for: ' + url));
      }
    });
    req.addEventListener('error', function(event, xhr) {
      console.log('rest error:', event, xhr);
      reject(Error('REST: Network error: ' + url));
    });
    if (PROXY)
      url = PROXY + encodeURI(url);

    req.open('GET', url, true);
    if (responseType)
      req.responseType = responseType;
    for (var h in headers) {
      req.setRequestHeader(h, headers[h]);
    }
    req.send(null);
  });
}

module.exports = {
  requestText: function(url, headers) {
    return new Promise(function(resolve, reject) {
      headers = headers || {};
      requestURL(url, headers).then(function(req) {
        resolve(req.responseText);
      }, reject);
    });
  },

  requestBinary: function(url, headers) {
    return new Promise(function(resolve, reject) {
      headers = headers || {};
      requestURL(url, headers, 'arraybuffer').then(function(req) {
        var arrayBuffer = req.response;
        if (!arrayBuffer)
          reject(Error('requestBinary: Unsuccessful binary request: ' + req));

        resolve(new Uint8Array(arrayBuffer));
      }, reject);
    });
  },

  requestJSON: function(url, headers) {
    return new Promise(function(resolve, reject) {
      headers = headers || {};
      requestURL(url, headers).then(function(req) {
        parseJSON(req).then(resolve, reject);
      }, reject);
    });
  },

  requestXML: function(url, extractionConfig, headers) {
    return new Promise(function(resolve, reject) {
      headers = headers || {};
      if (!extractionConfig)
        return {};

      requestURL(url, headers).then(function(req) {
        resolve(parseXML(req.responseXML, extractionConfig));
      }, reject);
    });
  }
};



